import {CellGroup, Cell, Row, Col, Divider, Button} from 'vant';
import 'vant/lib/cell-group/style';
import 'vant/lib/cell/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';
import 'vant/lib/divider/style';
import 'vant/lib/button/style';
import {AuthOriginalTxModel, AuthAdminType} from "../../model/original_address_model";

Vue.use(CellGroup).use(Cell).use(Row).use(Col).use(Divider).use(Button);

@Component({})

export default class OriginalProcess extends Vue {

    private authTypeValue = ""; // 授权或取消
    private authOriginalTxModel = new AuthOriginalTxModel();

    public created() {
        this.$store.state.title = "信息上链进度";

        if (this.$store.state.authOriginalTxModel.authType == AuthAdminType.AddAdmin) {
            this.authTypeValue = this.$t('authOriginal').toString();
        } else {
            this.authTypeValue = this.$t('cancelOriginal').toString();
        }
    }

    public mounted() {
        this.authOriginalTxModel = this.$store.state.authOriginalTxModel;
    }

    // todo 通过绑定store里面的数据格式，监听数据上的状态变化属性，来实现监听链上回调状态。
    // 目前没接口，不用考虑监听，暂时不做，状态写死
    @Watch('$route', {immediate: true, deep: true})
    public Change(toRouter: any, fromRouter: any) {
        //todo 动画 处理待定，先需要处理路由规则
        /*if (toRouter.meta.index > fromRouter.meta.index) {
            this.transitionName = 'slide-left';
        } else {
            this.transitionName = 'slide-right';
        }*/
    }

    public clickToHome(): void {
        this.$router.replace({name: 'cert_search'});
    }

}
